/* Amélioration des fade-in par scroll */
.fade-section {
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInScroll 1s ease-out forwards;
  animation-delay: 0.3s;
  margin-top: 4rem;
}

/* Différents délais pour effet escalier */
.countdown-section {
  animation-delay: 0.4s !important;
}

.features {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  animation-delay: 0.6s !important;
}

.feature {
  background: #1e1e1e;
  border-radius: 15px;
  padding: 1.5rem;
  width: 280px;
  box-shadow: 0 0 15px #ff416c44;
  transition: transform 0.3s ease;
}

.feature:hover {
  transform: scale(1.05);
  box-shadow: 0 0 25px #ff4b2bcc;
}

/* Countdown style */
.countdown {
  font-size: 2rem;
  font-weight: 700;
  margin-top: 0.5rem;
  color: #ff416c;
}

/* AnimatedTitle adjustments */
.animated-title {
  font-size: 4rem;
  font-weight: 900;
  letter-spacing: 0.1em;
  background: linear-gradient(270deg, #ff416c, #ff4b2b, #ff416c);
  background-size: 600% 600%;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  animation: gradientMove 6s ease infinite, float 3s ease-in-out infinite;
  transform-style: preserve-3d;
  perspective: 1000px;
  margin: 0 auto;
  max-width: 90vw;
}

/* Keyframes pour gradient et flottement */
@keyframes gradientMove {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes float {
  0% { transform: translateZ(0) translateY(0); }
  50% { transform: translateZ(20px) translateY(-15px); }
  100% { transform: translateZ(0) translateY(0); }
}

/* CTA Button animation */
.cta-button {
  background: linear-gradient(90deg, #ff416c, #ff4b2b);
  box-shadow: 0 4px 15px #ff416caa;
  font-size: 1.25rem;
  padding: 1rem 3rem;
  border-radius: 40px;
  border: none;
  color: white;
  cursor: pointer;
  transition: all 0.35s ease;
  user-select: none;
}

.cta-button:hover,
.cta-button:focus {
  background: linear-gradient(90deg, #ff4b2b, #ff416c);
  box-shadow: 0 6px 20px #ff4b2bcc;
  transform: scale(1.1);
  outline: none;
}

/* Responsive */
@media (max-width: 600px) {
  .animated-title {
    font-size: 2.5rem;
  }

  .feature {
    width: 90%;
  }

  .hero-subtitle {
    font-size: 1.2rem;
  }
}
